import React, { Component, Suspense } from "react";
import "../Styles/Summary.css"
import { S } from "selector_dom"
import { userData, data } from "../Data";
import { dateGetter, dateIndex } from "./MainPage";

const DailyDetails = React.lazy(() => import("../DailyDetails"));

export default class Summary extends Component {
  constructor(props) {
    super(props);

    const kcal = this.NutritionArray("kcal");
    const ArithmeticArray = [...kcal.filter((e) => e !== 0)];

    this.state = {
      kcal: kcal,
      carb: this.NutritionArray("carb"),
      protein: this.NutritionArray("protein"),
      fat: this.NutritionArray("fat"),

      breakfast: 0,
      lunch: 0,
      dinner: 0,
      snacks: 0,

      lastWeekKcalArithmetic:
        ArithmeticArray.length > 0 ? Math.round(ArithmeticArray.reduce((a, b) => a + b) / ArithmeticArray.length) : 0,
      arithmetic: ArithmeticArray.length,
    };
  }

  componentDidMount() {
    this.setState({
      breakfast: this.chartLastWeekPercentByMeal("breakfast"),
      lunch: this.chartLastWeekPercentByMeal("lunch"),
      dinner: this.chartLastWeekPercentByMeal("dinner"),
      snacks: this.chartLastWeekPercentByMeal("snacks"),
    });

    window.scrollTo({ top: 0 });
    this.chartLastWeekKcal();
    this.chartLastWeekNutritions();
    
    document.getElementById("summaryPage").style.opacity = "1";
  }

  NutritionArray = (nutrition) => {
    let res = [];
    for (let i = 0; i > -8; i--) {
      res.push(this.NutritionSum(nutrition, i));
    }
    return res;
  };

  NutritionSum = (nutrition, date) => {
    const getDataByDate = userData.diary.filter((e) => e.date === dateGetter(date))[0];
    let sum = 0;
    if (getDataByDate) {
      for (let i in getDataByDate) {
        if (i !== "date" && i !== "eaten") {
          const getDataById = getDataByDate[i].map(
            (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
          );

          sum += getDataById.map((e) => e[nutrition] * ((e.value * e.input) / 100)).reduce((a, b) => a + b);
        }
      }
    }
    return sum;
  };

  chartLastWeekKcal = () => {
    for (let i = 0; i > -8; i--) {
      const height = (this.state.kcal[i * -1] / userData.userInfo.dailykcal) * 100;
      S("#KcalChart" + i * -1).css({height: height + "%"});
      S("#KcalChart" + i * -1 + " p").text(Math.round(height) + "%");
    }
  };

  chartLastWeekNutritions = () => {
    for (let i = 0; i > -8; i--) {
      const heightCarb = (this.state.carb[i * -1] * 400) / userData.userInfo.dailykcal;
      const heightProtein = (this.state.protein[i * -1] * 400) / userData.userInfo.dailykcal;
      const heightFat = (this.state.fat[i * -1] * 900) / userData.userInfo.dailykcal;
      S("#NutritionschartsCarb" + i * -1).css({height: heightCarb + "%"});
      S("#NutritionschartsProtein" + i * -1).css({height: heightProtein + "%"});
      S("#NutritionschartsFat" + i * -1).css({height: heightFat + "%"});
      if (heightCarb + heightProtein + heightFat > 120)
        S("#Nutritionscharts" + i * -1)
          .css({overflow: "hidden", height: "120%"});
    }
  };

  NutritionSumByMeal = (nutrition, date, meal) => {
    const getDataByDate = userData.diary.filter((e) => e.date === dateGetter(date))[0];
    if (getDataByDate && getDataByDate[meal]) {
      const getDataById = getDataByDate[meal].map(
        (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
      );
      return getDataById.map((e) => e[nutrition] * ((e.value * e.input) / 100)).reduce((a, b) => a + b);
    } else return 0;
  };

  chartLastWeekPercentByMeal = (meal) => {
    let ArithmeticArray = [];
    for (let i = 0; i > -8; i--) {
      if (this.NutritionSumByMeal("kcal", i, meal) !== 0) ArithmeticArray.push(this.NutritionSumByMeal("kcal", i, meal));
    }
    if (ArithmeticArray.length > 0) {
      return Math.round(
        (ArithmeticArray.reduce((a, b) => a + b) / this.state.arithmetic / this.state.lastWeekKcalArithmetic) * 100
      );
    } else return 0;
  };

  NutritionSumByFood = (date, food) => {
    const getDataByDate = userData.diary.filter((e) => e.date === dateGetter(date))[0];
    let sum = 0;
    if (getDataByDate) {
      for (let i in getDataByDate) {
        if (i !== "date" && i !== "eaten") {
          const getDataById = getDataByDate[i].map(
            (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
          );
          sum += getDataById
            .map((e) => (e.description === food ? e.kcal * ((e.value * e.input) / 100) : 0))
            .reduce((a, b) => a + b);
        }
      }
    }
    return sum;
  };

  chartLastWeekPercentByFood = (food) => {
    let ArithmeticArray = [];
    for (let i = 0; i > -8; i--) {
      if (this.NutritionSumByFood(i, food) !== 0) ArithmeticArray.push(this.NutritionSumByFood(i, food));
    }
    if (ArithmeticArray.length > 0) {
      return Math.round(
        (ArithmeticArray.reduce((a, b) => a + b) / this.state.arithmetic / this.state.lastWeekKcalArithmetic) * 100
      );
    } else return 0;
  };

  render() {
    return (
      <div id="summaryPage">
        <header id="summaryPageHeader">
          <p id="summaryPageHeaderTitle">الــمــوجــز</p>
        </header>
        <div id="summaryLastWeekKcalContainer">
          <p id="summaryLastWeekKcalTitle">السعرات المستهلكة لأسبوع</p>
          <div id="summaryLastWeekKcalNumContainer">
            <p>{userData.userInfo.dailykcal}</p>
            <p>{Math.round(userData.userInfo.dailykcal / 2)}</p>
            <p>0</p>
          </div>
          <div id="summaryLastWeekKcalChartContainer">
            <div id="chartMiddleLine"></div>
            <div className="Kcalcharts" id="KcalChart0">
              <p></p>
            </div>
            <div className="Kcalcharts" id="KcalChart1">
              <p></p>
            </div>
            <div className="Kcalcharts" id="KcalChart2">
              <p></p>
            </div>
            <div className="Kcalcharts" id="KcalChart3">
              <p></p>
            </div>
            <div className="Kcalcharts" id="KcalChart4">
              <p></p>
            </div>
            <div className="Kcalcharts" id="KcalChart5">
              <p></p>
            </div>
            <div className="Kcalcharts" id="KcalChart6">
              <p></p>
            </div>
          </div>
        </div>
        <div id="summaryLastWeekNutritionsContainer">
          <p id="summaryLastWeekNutritionsTitle">متوسط السعرات حسب المغذيات لأسبوع</p>
          <div id="summaryLastWeekNutritionsNumContainer">
            <p>100%</p>
            <p>50%</p>
            <p>0%</p>
          </div>
          <div id="summaryLastWeekNutritionsChartContainer">
            <div id="chartMiddleLine"></div>
            <div className="Nutritionscharts" id="Nutritionscharts0">
              <div id="NutritionschartsCarb0"></div>
              <div id="NutritionschartsProtein0"></div>
              <div id="NutritionschartsFat0"></div>
            </div>
            <div className="Nutritionscharts" id="Nutritionscharts1">
              <div id="NutritionschartsCarb1"></div>
              <div id="NutritionschartsProtein1"></div>
              <div id="NutritionschartsFat1"></div>
            </div>
            <div className="Nutritionscharts" id="Nutritionscharts2">
              <div id="NutritionschartsCarb2"></div>
              <div id="NutritionschartsProtein2"></div>
              <div id="NutritionschartsFat2"></div>
            </div>
            <div className="Nutritionscharts" id="Nutritionscharts3">
              <div id="NutritionschartsCarb3"></div>
              <div id="NutritionschartsProtein3"></div>
              <div id="NutritionschartsFat3"></div>
            </div>
            <div className="Nutritionscharts" id="Nutritionscharts4">
              <div id="NutritionschartsCarb4"></div>
              <div id="NutritionschartsProtein4"></div>
              <div id="NutritionschartsFat4"></div>
            </div>
            <div className="Nutritionscharts" id="Nutritionscharts5">
              <div id="NutritionschartsCarb5"></div>
              <div id="NutritionschartsProtein5"></div>
              <div id="NutritionschartsFat5"></div>
            </div>
            <div className="Nutritionscharts" id="Nutritionscharts6">
              <div id="NutritionschartsCarb6"></div>
              <div id="NutritionschartsProtein6"></div>
              <div id="NutritionschartsFat6"></div>
            </div>
          </div>
          <div id="summaryLastWeekNutritionsListContainer">
            <p>الكربوهيدرات</p>
            <p>البروتين</p>
            <p>الدهون</p>
          </div>
        </div>
        <div id="summaryLastWeekKcalArithmeticContainer">
          <p id="summaryLastWeekKcalArithmeticTitle">متوسط السعرات المستهلكة لأسبوع</p>
          <p id="summaryLastWeekKcalArithmetic">
            {this.state.lastWeekKcalArithmetic}
            <span>سعرة حرارية</span>
          </p>
        </div>
        <div id="summaryLastWeekKcalByMealContainer">
          <p id="summaryLastWeekKcalByMealTitle">متوسط السعرات حسب الوجبة لأسبوع</p>
          <div id="summaryByMealDetailsContainer">
            <div
              className="pie"
              style={{
                display: this.state.breakfast + this.state.lunch + this.state.dinner + this.state.snacks === 0 ? "none" : "block",
                background: `
						conic-gradient(
						#003f5c ${this.state.breakfast}%, 
						#7a5195 0%, 
						#7a5195 ${this.state.breakfast + this.state.lunch}%, 
						#ef5675 0%, 
						#ef5675 ${this.state.breakfast + this.state.lunch + this.state.dinner}%, 
						#ffa600 0%,
						#ffa600 ${this.state.breakfast + this.state.lunch + this.state.dinner + this.state.snacks}%,
						var(--background-white) 0%)
						`,
              }}
            >
              <div></div>
            </div>
            <div id="summaryByMealDetailsTitlesContainer">
              <p id="summaryByMealDetailsBreakfast">الفطور</p>
              <p id="summaryByMealDetailsLunch">الغداء</p>
              <p id="summaryByMealDetailsDinner">العشاء</p>
              <p id="summaryByMealDetailsSnacks">وجبة خفيفة</p>
            </div>
            <div id="summaryByMealDetailsPercentContainer">
              <p>
                {this.state.breakfast}
                <span>%</span>
              </p>
              <p>
                {this.state.lunch}
                <span>%</span>
              </p>
              <p>
                {this.state.dinner}
                <span>%</span>
              </p>
              <p>
                {this.state.snacks}
                <span>%</span>
              </p>
            </div>
          </div>
        </div>
        <div id="summaryLastWeekKcalByFoodContainer">
          <p id="summaryLastWeekKcalByFoodTitle">متوسط السعرات حسب النوع لأسبوع</p>
          <div id="summaryByFoodDetailsContainer">
            <div id="summaryByFoodDetailsTitlesContainer">
              <p>الأسماك</p>
              <p>البيض ومنتجات الحليب</p>
              <p>البقوليات</p>
              <p>البذور</p>
              <p>الحبوب</p>
              <p>الحلويات</p>
              <p>الخضروات</p>
              <p>الدبس</p>
              <p>الزيت والدهون</p>
              <p>الفروج</p>
              <p>الفواكه</p>
              <p>المعجنات</p>
              <p>المكسرات</p>
              <p>المكملات غذائية</p>
            </div>
            <div id="summaryByFoodDetailsPercentContainer">
              <p>
                {this.chartLastWeekPercentByFood("أسماك")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("البيض ومنتجات الحليب")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("بقوليات")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("بذور")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("حبوب")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("حلويات")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("خضار")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("دبس")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("الزيت والدهون")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("فروج")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("فاكهة")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("معجنات")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("مكسرات")}
                <span>%</span>
              </p>
              <p>
                {this.chartLastWeekPercentByFood("مكملات غذائية")}
                <span>%</span>
              </p>
            </div>
          </div>
        </div>
        <div id="dailyDetailsContainer">
          <Suspense fallback="" key={Math.random() * 100}>
            <DailyDetails key={Math.random() * 1000} date={dateGetter(dateIndex)} />
          </Suspense>
        </div>
      </div>
    );
  }
}
