import React, { Component } from "react";
import "./Styles/Loading.css";
import { S } from "selector_dom"

let tl;
export default class Loading extends Component {

  componentDidMount() {
    this.loadingAnimation();
  };

  async componentDidUpdate() {
    if (this.props.end) {
      S("#fillPageColor").css({ transform: "translate(-50%, -50%) scale(221)" });
      await S().wait(300);
      clearInterval(tl);
      S("#loadingPage").css({ display: "none" });
      S("#loadingPage svg").remove();
    }
  };

  loadingAnimation = () => {
    const el = document.getElementById("laodingTxt");
    let counter = -1;
    tl = setInterval(() => {
      counter++;
      counter === 0
        ? (el.innerHTML = "جاري التحميل ")
        : counter === 1
        ? (el.innerHTML = "جاري التحميل .")
        : counter === 2
        ? (el.innerHTML = "جاري التحميل ..")
        : counter === 3
        ? (el.innerHTML = "جاري التحميل ...")
        : (counter = -1);
    }, 400);
  };

  render() {
    return (
      <div id="loadingPage" style={{ height: window.innerHeight + "px" }}>
        <svg id="svg10" viewBox="0 0 200 200" xmlns="http://www.w3.org/2000/svg">
          <path d="M196.999 100C197 99.8334 197 99.6668 197 99.5C197 45.6522 153.348 2 99.5 2C45.6522 2 2 45.6522 2 99.5C2 99.6668 2.00042 99.8334 2.00126 100" />
        </svg>
        <svg id="svg9" viewBox="0 0 200 200" xmlns="http://www.w3.org/2000/svg">
          <path d="M196.999 100C197 99.8334 197 99.6668 197 99.5C197 45.6522 153.348 2 99.5 2C45.6522 2 2 45.6522 2 99.5C2 99.6668 2.00042 99.8334 2.00126 100" />
        </svg>
        <svg id="svg8" viewBox="0 0 200 200" xmlns="http://www.w3.org/2000/svg">
          <path d="M196.999 100C197 99.8334 197 99.6668 197 99.5C197 45.6522 153.348 2 99.5 2C45.6522 2 2 45.6522 2 99.5C2 99.6668 2.00042 99.8334 2.00126 100" />
        </svg>
        <svg id="svg7" viewBox="0 0 200 200" xmlns="http://www.w3.org/2000/svg">
          <path d="M196.999 100C197 99.8334 197 99.6668 197 99.5C197 45.6522 153.348 2 99.5 2C45.6522 2 2 45.6522 2 99.5C2 99.6668 2.00042 99.8334 2.00126 100" />
        </svg>
        <svg id="svg6" viewBox="0 0 200 200" xmlns="http://www.w3.org/2000/svg">
          <path d="M196.999 100C197 99.8334 197 99.6668 197 99.5C197 45.6522 153.348 2 99.5 2C45.6522 2 2 45.6522 2 99.5C2 99.6668 2.00042 99.8334 2.00126 100" />
        </svg>
        <svg id="svg5" viewBox="0 0 200 200" xmlns="http://www.w3.org/2000/svg">
          <path d="M196.999 100C197 99.8334 197 99.6668 197 99.5C197 45.6522 153.348 2 99.5 2C45.6522 2 2 45.6522 2 99.5C2 99.6668 2.00042 99.8334 2.00126 100" />
        </svg>
        <svg id="svg4" viewBox="0 0 200 200" xmlns="http://www.w3.org/2000/svg">
          <path d="M196.999 100C197 99.8334 197 99.6668 197 99.5C197 45.6522 153.348 2 99.5 2C45.6522 2 2 45.6522 2 99.5C2 99.6668 2.00042 99.8334 2.00126 100" />
        </svg>
        <svg id="svg3" viewBox="0 0 200 200" xmlns="http://www.w3.org/2000/svg">
          <path d="M196.999 100C197 99.8334 197 99.6668 197 99.5C197 45.6522 153.348 2 99.5 2C45.6522 2 2 45.6522 2 99.5C2 99.6668 2.00042 99.8334 2.00126 100" />
        </svg>
        <svg id="svg2" viewBox="0 0 200 200" xmlns="http://www.w3.org/2000/svg">
          <path d="M196.999 100C197 99.8334 197 99.6668 197 99.5C197 45.6522 153.348 2 99.5 2C45.6522 2 2 45.6522 2 99.5C2 99.6668 2.00042 99.8334 2.00126 100" />
        </svg>
        <svg id="svg1" viewBox="0 0 200 200" xmlns="http://www.w3.org/2000/svg">
          <path d="M196.999 100C197 99.8334 197 99.6668 197 99.5C197 45.6522 153.348 2 99.5 2C45.6522 2 2 45.6522 2 99.5C2 99.6668 2.00042 99.8334 2.00126 100" />
        </svg>
        <p id="laodingTxt">جاري التحميل</p>
        <div id="fillPageColor"></div>
      </div>
    );
  }
}
