import React, { Component, Fragment, Suspense } from "react";
import "../Styles/MainPage.css";

import { wait, ProgressBar } from "../dependencies";
import { S } from "selector_dom"
import { updateRenderState } from "..";
import { gsap } from "gsap";
import { userData, data, tips } from "../Data";
import Diary from "../Diary";

const SearchPage = React.lazy(() => import("../SearchPage/SearchPage"));
const DailyDetails = React.lazy(() => import("../DailyDetails"));

let [AddIsPressable, arrowIsPressable, isReversed] = [true, true, false];
let openAddMenuAnim;

export let dateIndex = 0;
export function dateGetter(i) {
  const days = ["الأحد", "الاثنين", "الثلاثاء", "الاربعاء", "الخميس", "الجمعة", "السبت"];
  const newDate = new Date();
  const newDecrementDate = new Date(newDate.getFullYear(), newDate.getMonth(), newDate.getDate() + i);
  const getYear = newDecrementDate.getFullYear();
  const getMonth = newDecrementDate.getMonth() + 1;
  const getDate = newDecrementDate.getDate();
  const getDay = newDecrementDate.getDay();
  return `${getYear} / ${getMonth} / ${getDate} , ${days[getDay]}`;
}

function randomNumber(min, max) {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

const tipsNum = randomNumber(0, tips.length - 1);
let isTipsOn = true;

class MainPage extends Component {
  state = {
    date: dateIndex === 0 ? "اليوم" : dateIndex === 1 ? "الغد" : dateIndex === -1 ? "البارحة" : dateGetter(dateIndex),
  };

  componentDidMount() {
    this.setRemainPercent();
    this.responsiveResizing();

    window.addEventListener("resize", this.responsiveResizing);
    window.addEventListener("scroll", this.hideHeaderScrollAnimation);

    // check if tips are allowed to show
    if (!isTipsOn || window.localStorage.getItem("tips") === "false") S("#tipsContainer").css({ display: "none" });

    // fade intro animation
    document.getElementById("MainPage").style.opacity = "1";
  }

  componentDidUpdate() {
    this.setRemainPercent();
  }

  componentWillUnmount() {
    window.removeEventListener("scroll", this.hideHeaderScrollAnimation);
    window.removeEventListener("resize", this.responsiveResizing);
    window.removeEventListener("resize", this.resizeCircleProgressHandler);
  }

  NutritionSum = (nutrition) => {
    const getDataByDate = userData.diary.filter((e) => e.date === dateGetter(dateIndex))[0];
    let sum = 0;
    if (getDataByDate) {
      for (let i in getDataByDate) {
        if (i !== "date" && i !== "eaten") {
          const getDataById = getDataByDate[i].map(
            (e) => (e = Object.assign({}, data.filter((el) => el.id === e[0])[0], { value: e[1], input: e[2] }))
          );

          sum += getDataById.map((e) => e[nutrition] * ((e.value * e.input) / 100)).reduce((a, b) => a + b);
        }
      }
    }
    return sum;
  };

  remainKcalSum = () => Math.round(userData.userInfo.dailykcal - this.NutritionSum("kcal"));
  eatenKcalPercent = () => Math.round((Math.round(this.NutritionSum("kcal")) / userData.userInfo.dailykcal) * 100);

  remainCarbSum = () => Math.round(userData.userInfo.dailyCarbs - this.NutritionSum("carb"));
  carbPercent = () => Math.round((Math.round(this.NutritionSum("carb")) / userData.userInfo.dailyCarbs) * 100);

  remainProteinSum = () => Math.round(userData.userInfo.dailyProtein - this.NutritionSum("protein"));
  proteinPercent = () => Math.round((Math.round(this.NutritionSum("protein")) / userData.userInfo.dailyProtein) * 100);

  remainFatSum = () => Math.round(userData.userInfo.dailyFat - this.NutritionSum("fat"));
  fatPercent = () => Math.round((Math.round(this.NutritionSum("fat")) / userData.userInfo.dailyFat) * 100);

  diarySlideAnimation = async (diraction) => {
    if (arrowIsPressable) {
      arrowIsPressable = false;
      const page = S("#pageBody");
      const clonedEl = page.clone(true);
      const diaryContainer = S("#diaryContainer");
      const scrollBy = diaryContainer.getStyle("width", true) + 1;

      if (diraction === "toLeft") {
        diaryContainer.prepend(clonedEl);
        await wait(10);
        diaryContainer.scrollTo({ byX: -scrollBy, behavior: "smooth", pps: 35 });
      } else if (diraction === "toRight") {
        diaryContainer.append(clonedEl);
        diaryContainer.scrollTo({ byX: -scrollBy });
        await wait(10);
        diaryContainer.scrollTo({ byX: scrollBy, behavior: "smooth", pps: 35 });
      }
      await wait(300);
      clonedEl.remove();
      arrowIsPressable = true;
    }
  };

  checkDate = (decrement = false) => {
    if (arrowIsPressable) {
      decrement ? dateIndex-- : dateIndex++;
      if (dateIndex === 0) {
        this.setState({ date: "اليوم" });
      } else if (dateIndex === 1) {
        this.setState({ date: "الغد" });
      } else if (dateIndex === -1) {
        this.setState({ date: "البارحة" });
      } else {
        this.setState({ date: dateGetter(dateIndex) });
      }
      decrement ? this.diarySlideAnimation("toRight") : this.diarySlideAnimation("toLeft");
    }
  };

  mountSearchComponent = (title, hash) => {
    isReversed = false;
    updateRenderState(
      <Suspense fallback="" key={Math.random() * 100}>
        <SearchPage key={Math.random() * 100} title={title} doAnimation={true} />
      </Suspense>
    );
    window.history.pushState("", "", "/search#" + hash);
  };

  hideHeaderScrollAnimation = () => {
    const getHeaderHeight = S("#header").getStyle("height", true) / 1.6;
    let multiplier = (1 / getHeaderHeight) * (getHeaderHeight - window.scrollY);
    multiplier = multiplier < 0 ? 0 : multiplier > 1 ? 1 : multiplier;
    S(".HideOnScrollElements").css({ transform: `scale(${multiplier})`, opacity: multiplier });
  };

  responsiveResizing = () => {
    // for the header sticky postion from the top
    const getHeaderHeight = S("#header").getStyle("height", true);
    S("#header").css({ top: -getHeaderHeight + 55 + "px" });

    // for add buttons postion from the left
    const getSideWidth = (window.innerWidth - S("#MainPage").getStyle("width", true)) / 2;
    const calcLeft = ((getSideWidth + 45) * 100) / window.innerWidth + "%";
    if (!isReversed) S(".floatButton").css({ left: calcLeft });

    // for macros percentage bars position from the bottom
    const getHeaderTitleHeight = S("#title").getStyle("height", true);
    const getHeaderPercentNumHeight = S(".percentNum").getStyle("height", true);
    const calcBottom = getHeaderTitleHeight + getHeaderPercentNumHeight + 38 + "px";
    S("#macrosPercent").css({ bottom: calcBottom });
  };

  animateAddFoodButton = async () => {
    const animDuration = 0.15;
    if (!isReversed && AddIsPressable) {
      openAddMenuAnim = gsap.timeline();
      AddIsPressable = false;

      // hide puls animation
      S("#addButtonPulsAnimation1, #addButtonPulsAnimation2").css({ display: "none" });

      openAddMenuAnim
        .to("#addFoodBG", { duration: animDuration, opacity: 1, display: "block" }, "a")
        .to("#addButton svg", { duration: animDuration, rotate: 135 }, "b")
        .to("#addButton", { duration: animDuration, scale: 1.03, backgroundColor: "var(--main-color)" }, "b")
        .to("#addButton", { duration: animDuration, scale: 1, backgroundColor: "var(--second-color)" }, "c")
        .to("#addSnacks", { duration: animDuration, display: "block", bottom: "calc(50% - 75px)", left: "50%", scale: 1, delay: 0.1 }, "b")
        .to("#addDinner", { duration: animDuration, display: "block", bottom: "50%", left: "calc(50% - 75px)", scale: 1, delay: 0.2 }, "b")
        .to("#addLaunch", { duration: animDuration, display: "block", bottom: "50%", left: "calc(50% + 75px)", scale: 1, delay: 0.3 }, "b")
        .to("#addBreakfast", { duration: animDuration, display: "block", bottom: "calc(50% + 75px)", left: "50%", scale: 1 }, "b");

      isReversed = true;
      await wait(600);
      AddIsPressable = true;
    } else if (isReversed && AddIsPressable) {
      this.responsiveResizing();
      AddIsPressable = false;

      openAddMenuAnim.reverse();
      isReversed = false;
      await wait(600);
      AddIsPressable = true;

      // show puls animation
      S("#addButtonPulsAnimation1, #addButtonPulsAnimation2").removeAttr("style");
    }
  };

  setRemainPercent = () => {
    const getHeaderWidth = S("#header").getStyle("width", true) / 3 / 2;
    ProgressBar("#remainProgress", {
      input: this.eatenKcalPercent() > 100 ? this.eatenKcalPercent() - 100 : this.eatenKcalPercent(),
      R: getHeaderWidth,
      width: 5,
      fillColor: "none",
      backgroundColor: this.eatenKcalPercent() > 100 ? "grad" : "rgb(250,250,245,0.3)",
      textColor: this.eatenKcalPercent() > 100 ? "#F3D737" : "white",
      beforeText: this.eatenKcalPercent() > 100 ? "+ " : "",
      afterText: "%",
      progressColor: this.eatenKcalPercent() > 100 ? "#F3D737" : "grad",
      gradColor1: "#FFFEFF",
      gradColor2: "#D7FFFE",
      doAnimation: this.props.doAnimation ? "yes" : "no",
    });

    window.addEventListener("resize", this.resizeCircleProgressHandler);
  };

  resizeCircleProgressHandler = () => {
    const getHeaderWidth = S("#header").getStyle("width", true) / 3 / 2;
    const input = this.eatenKcalPercent() > 100 ? this.eatenKcalPercent() - 100 : this.eatenKcalPercent();
    S("#remainProgress svg")
      .attr("height", getHeaderWidth * 2 + 8)
      .attr("width", getHeaderWidth * 2 + 8);
    S("#remainProgress svg circle")
      .attr("r", getHeaderWidth)
      .attr("cx", getHeaderWidth + 4)
      .attr("cy", getHeaderWidth + 4);
    S("#remainProgress svg text")
      .css({ fontSize: getHeaderWidth / 2 + "px" })
      .attr("dy", getHeaderWidth / 6);
    const calcProgress = 2 * Math.PI * getHeaderWidth - (input * (2 * Math.PI * getHeaderWidth)) / 100;
    S("#remainProgress svg circle:nth-child(3)").css({
      strokeDashoffset: calcProgress + "px",
      strokeDasharray: 2 * Math.PI * getHeaderWidth + "px",
    });
  };

  DailyDetailsHandle = () => {
    updateRenderState(
      <Suspense fallback="" key={Math.random() * 100}>
        <DailyDetails key={Math.random() * 1000} date={dateGetter(dateIndex)} />
      </Suspense>
    );
    window.history.pushState("", "", "/DailyDetailsHandle");
  };

  render() {
    return (
      <Fragment>
        <div id="addFoodBG" onClick={this.animateAddFoodButton}></div>
        <div id="MainPage">
          <h2 id="title">سعراتي اليومية</h2>
          <div id="header">
            <svg width="100%" viewBox="0 0 320 300" style={{ filter: "drop-shadow(0px 0px 5px rgb(0, 0, 0, 0.5))" }}>
              <linearGradient id="Gradient1" gradientTransform="rotate(45)">
                <stop offset="20%" />
                <stop offset="100%" />
              </linearGradient>
              <path d="M0 0H320V286.491C320 286.491 249 300 159 300C69 300 0 286.491 0 286.491V0Z" fill="url(#Gradient1)" />
            </svg>
            <div className="remainValues">
              <div className="caloriesEaten">
                <p className="HideOnScrollElements">{Math.round(this.NutritionSum("kcal"))}</p>
                <p className="HideOnScrollElements">الـمـأكـولة</p>
              </div>
              <div id="remainProgress" className="HideOnScrollElements"></div>
              <div className="caloriesRemain">
                <p className="HideOnScrollElements" style={{ color: this.remainKcalSum() < 0 ? "#F3D737" : "white" }}>
                  {this.remainKcalSum() < 0 ? this.remainKcalSum() * -1 : this.remainKcalSum()}
                </p>
                <p className="HideOnScrollElements">{this.remainKcalSum() < 0 ? "زائــدة" : "المتبـقية"}</p>
              </div>
            </div>
            <div id="macrosPercent">
              <div className="macrosPercentContainer HideOnScrollElements">
                <p className="macroName">الكربوهيدرات</p>
                <div className="macrosPercentLine" id="macrosPercentLineCarbs" style={{ width: this.carbPercent() + "%" }}></div>
                <div
                  className="macrosPercentLine"
                  style={{
                    backgroundColor: "#F3D737",
                    backgroundImage: "initial",
                    width: (this.carbPercent() > 100 ? this.carbPercent() - 100 : 0) + "%",
                  }}
                ></div>
                <p className="percentNum">
                  <span style={{ display: this.remainCarbSum() < 0 ? "none" : "initial" }}>باقي </span>
                  {this.remainCarbSum() < 0 ? this.remainCarbSum() * -1 : this.remainCarbSum()}
                  <span>غ</span>
                  <span style={{ display: this.remainCarbSum() < 0 ? "initial" : "none" }}> زائدة</span>
                </p>
              </div>
              <div className="macrosPercentContainer HideOnScrollElements">
                <p className="macroName">البروتين</p>
                <div className="macrosPercentLine" id="macrosPercentLineProtein" style={{ width: this.proteinPercent() + "%" }}></div>
                <div
                  className="macrosPercentLine"
                  style={{
                    backgroundColor: "#F3D737",
                    backgroundImage: "initial",
                    width: this.proteinPercent() > 100 ? this.proteinPercent() - 100 : 0 + "%",
                  }}
                ></div>
                <p className="percentNum">
                  <span style={{ display: this.remainProteinSum() < 0 ? "none" : "initial" }}>باقي </span>
                  {this.remainProteinSum() < 0 ? this.remainProteinSum() * -1 : this.remainProteinSum()}
                  <span>غ</span>
                  <span style={{ display: this.remainProteinSum() < 0 ? "initial" : "none" }}> زائدة</span>
                </p>
              </div>
              <div className="macrosPercentContainer HideOnScrollElements">
                <p className="macroName">الدهون</p>
                <div className="macrosPercentLine" id="macrosPercentLineFat" style={{ width: this.fatPercent() + "%" }}></div>
                <div
                  className="macrosPercentLine"
                  style={{
                    backgroundColor: "#F3D737",
                    backgroundImage: "initial",
                    width: this.fatPercent() > 100 ? this.fatPercent() - 100 : 0 + "%",
                  }}
                ></div>
                <p className="percentNum">
                  <span style={{ display: this.remainFatSum() < 0 ? "none" : "initial" }}>باقي </span>
                  {this.remainFatSum() < 0 ? this.remainFatSum() * -1 : this.remainFatSum()}
                  <span>غ</span>
                  <span style={{ display: this.remainFatSum() < 0 ? "initial" : "none" }}> زائدة</span>
                </p>
              </div>
            </div>
            <svg
              id="arrowDailyDetails"
              className="HideOnScrollElements"
              onClick={this.DailyDetailsHandle}
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
            >
              <path d="M7.41 8.59L12 13.17l4.59-4.58L18 10l-6 6-6-6 1.41-1.41z" />
            </svg>
          </div>
          <div id="dateSelector">
            <svg
              id="dateToRightArrow"
              className="dateArrow"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              onClick={() => this.checkDate(true)}
            >
              <path d="M5 3l3.057-3 11.943 12-11.943 12-3.057-3 9-9z" />
            </svg>
            <p>{this.state.date}</p>
            <svg
              id="dateToLeftArrow"
              className="dateArrow"
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              transform="rotate(180)"
              style={{ transform: "rotate(180deg)" }}
              onClick={() => this.checkDate(false)}
            >
              <path d="M5 3l3.057-3 11.943 12-11.943 12-3.057-3 9-9z" />
            </svg>
          </div>
          <div id="tipsContainer">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              viewBox="0 0 24 24"
              onClick={() => {
                isTipsOn = false;
                // const el = document.getElementById("tipsContainer");
                // const getheight = window.getComputedStyle(el).getPropertyValue("height");

                // el.style.height = getheight;
                // await wait(20);
                // el.style.height = "0px";
                // el.style.margin = "0px auto";
                // el.style.padding = "0px";
                // await wait(400);
                // el.style.display = "none";
                S("#tipsContainer").animate(
                  {},
                  { height: "0px", margin: "0px auto", padding: "0px" },
                  { duration: 400, dispalyNone: true, cleanUp: true, ease: "ease-out" }
                );
              }}
            >
              <path d="M24 20.188l-8.315-8.209 8.2-8.282-3.697-3.697-8.212 8.318-8.31-8.203-3.666 3.666 8.321 8.24-8.206 8.313 3.666 3.666 8.237-8.318 8.285 8.203z" />
            </svg>
            <p>نــصــائــح عــامــة</p>
            <p>{tips[tipsNum]}</p>
          </div>
          <div id="diaryContainer">
            <Diary date={dateGetter(dateIndex)} />
          </div>
          <div id="addButton" onClick={this.animateAddFoodButton}>
            <svg width="24" height="24" viewBox="0 0 24 24">
              <path d="M24 10h-10v-10h-4v10h-10v4h10v10h4v-10h10z" fill="white" />
            </svg>
            <div id="addButtonPulsAnimation1"></div>
            <div id="addButtonPulsAnimation2"></div>
          </div>
          <div id="addBreakfast" className="circleButton floatButton" onClick={() => this.mountSearchComponent("الفطور", "breakfast")}>
            <img src={require("../assets/bread.png")} alt="bread" />
            <p>الفطور</p>
          </div>
          <div id="addLaunch" className="circleButton floatButton" onClick={() => this.mountSearchComponent("الغداء", "lunch")}>
            <img src={require("../assets/soup.png")} alt="soup" />
            <p>الغداء</p>
          </div>
          <div id="addDinner" className="circleButton floatButton" onClick={() => this.mountSearchComponent("العشاء", "dinner")}>
            <img src={require("../assets/fish.png")} alt="fish" />
            <p>العشاء</p>
          </div>
          <div id="addSnacks" className="circleButton floatButton" onClick={() => this.mountSearchComponent("وجبة خفيفة", "snacks")}>
            <img src={require("../assets/banana.png")} alt="banana" />
            <p>وجبة خفيفة</p>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default MainPage;
